import React, {Component} from 'react';
import '../styles/App.scss';

class Dashboard extends Component {
    render() {
        return (
            <div>
                <div className="centered-container mt-5">
                    <h2>Hmm... you seem to be lost. This page doesn't exist. Go back <a href="http://gabrielaradu.me">home.</a></h2>
                </div>
                <div className="centered-container mt-3">
                    <a href="http://gabrielaradu.me/" rel="noopener noreferrer" target="_blank"><img
                        src={require('../img/undraw_page_not_found.svg')} alt="Invitation was sent with success"/>
                    </a>
                </div>
            </div>
        );
    }
}

export default Dashboard;
