import React from "react";
import {BrowserRouter, Route, Switch} from "react-router-dom";
import NotFound from "./NotFound";

const Router = () => (

    <BrowserRouter>
        <div>
            <Switch>
                <Route exact path="/login" component={NotFound}/>
                <Route component={NotFound}/>
            </Switch>
        </div>
    </BrowserRouter>

);
export default Router;